import React from "react";
import "./Blurb.css";

const Blurb = props => {
  return (
    <div className="blurb__wrapper">
      <h1 className="blurb__title"> {props.title} </h1>
      {props.expanded ? props.children : null}
    </div>
  );
};

export default Blurb;

