import React from 'react';
import './BotnavItem.css'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import {
    faFacebookF,
    faGithub,
    faFacebookMessenger,
    faLinkedin
} from '@fortawesome/free-brands-svg-icons';

const BotnavItem = (props) => {

    let iconComponent = null;
    switch (props.icon) {
        case 'facebook':
            iconComponent = faFacebookF;
            break;
        case 'github':
            iconComponent = faGithub;
            break;
        case 'linkedin':
            iconComponent = faLinkedin;
            break;
        case 'mail':
            iconComponent = faEnvelope;
            break;
        case 'messenger':
            iconComponent = faFacebookMessenger;
            break;
        default:
            return null;    
    }

    return (
        <div className='botnavItem'>
            <a className={props.icon} href={props.url}>
                <FontAwesomeIcon icon={iconComponent}/>
            </a>
        </div>
    );
}

export default BotnavItem;
