import React from "react";
import ReactDelayRender from "react-delay-render";
import FadeIn from "react-fade-in";

import "./AboutMe.css";

const AboutMe = () => {
  return (
    <FadeIn>
      <div className="about__wrapper">
        <p className="about__paragraph">
          Hi and thank you for visiting my page!
        </p>
        <p className="about__paragraph">
          My name is Filip. I am a Polish guy currently residing in Cork,
          Ireland. I am aspiring to become a Junior Front End Developer.
        </p>
        <p className="about__paragraph">
          This simple page is just a business card/repository for all of my
          projects from the beginning of my coding journey.
        </p>
        <p className="about__paragraph">
          If you wish to get in touch, shoot me an email!
        </p>
      </div>
    </FadeIn>
  );
};

export default ReactDelayRender({ delay: 401 })(AboutMe);
