import React from 'react';
import './Toolbar.css'
// import Logo from './Logo/Logo'

//components
// import DrawerToggleButton from '../SideDrawer/DrawerToggleButton';

//logo
import logo from '../../assets/icons/fz.png'


const toolbar = (props) => {
    return (
        <header className="toolbar">
            <nav className='toolbar__navigation spacer'>
                
                <div className='toolbar__logo'>
                    <a href='/'>
                        <img alt='FZ' className='toolbar__logo-image' src={logo}/>
                    </a>
                </div>
                
                <div className='spacer' />
                {/* <Logo/> */}
                <div className='spacer' />
                {/* <div className='toolbar__navigation-items'>
                    <ul>
                        <li>ABOUT</li>
                        <li>PROJECTS</li>
                    </ul>
                </div>
                <div>
                    <DrawerToggleButton clicked={props.click}/>
                </div> */}
            </nav>
        </header>        
    );
}

export default toolbar;
